.textBlock {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0; }
  .textBlock::after {
    content: '';
    height: 100%;
    vertical-align: middle;
    display: inline-block; }
  .textBlock__text {
    display: inline-block;
    vertical-align: middle;
    white-space: pre-wrap;
    word-break: break-word;
    max-width: 100%; }
  .textBlock_shadow {
    text-shadow: 0 1px 3px black; }
  .textBlock_valign_top::after, .textBlock_valign_top .textBlock__text {
    vertical-align: top; }
  .textBlock_valign_bottom::after, .textBlock_valign_bottom .textBlock__text {
    vertical-align: bottom; }



