    background-size: 10em;
    background-position: center center;
    background-repeat: no-repeat;
    opacity: .2;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
    -webkit-transform: rotate(15deg);
        -ms-transform: rotate(15deg);
            transform: rotate(15deg);
